/***/ (function(module, exports) {

	module.exports = {"data":{"post":{"html":"<p><a href=\"http://www.rosen-direct.de/shop/catalog/images/jubile_du_prince_de_monaco.jpg\"><img alt=\"jubile_du_prince_de_monaco.jpg\" src=\"http://www.papascott.de/wordpress/wp-content/uploads/2005/06/jubile_du_prince_de_monaco.jpg\" width=\"100\" height=\"100\" border=\"0\" align=\"right\" /></a> This year I thought I had the problem of Mama's birthday present licked. I'd let Christopher take care of it! I had asked him early in the week what we should get, and he immediately answered \"A rose\". The perfect answer, since Mama loves her garden and she would love any present that came from Christopher.</p>\n<p>However, when it came time to go to the garden shop on Thursday to pick one out, Christopher participated only unwillingly (he was sulking because I wouldn't let him play on the <a href=\"http://www.playmobil.de/\">Playmobil web site</a> until after supper). However, with the promise of an ice cream bar, he did come along and immediately picked out a classical <a href=\"http://www.rosen-direct.de/shop/catalog/product_info.php/products_id/449\">Jubilee du Prince de Monaco</a> rose bush. However, that didn't end his sulk. I hid the rose in our back yard and waited for Mama's birthday on Saturday.</p>\n<p>On Saturday morning, it was wet and windy outside and Christopher couldn't be dragged away from his knight and dragon playthings. I had hoped that we would give Mama her rose together, but he just blurted out \"Happy Birthday\" and headed back to his room. I was left alone to head out in the rain to get the rose and explain to Mama the story.  She seemed to enjoy it, but it wasn't quite the effect I had hoped for.</p>\n<p>Meanwhile today we have the family birthday (Mama and two nephews) celebration. We were planning an American-style barbecue, however we have a streak of attracting rain showers every time we plan to barbecue. So instead of cooking on the patio we will be shivering in the carport. Again.</p>","fields":{"title":"A Rose From Christopher","date":"12 Jun 2005","url":"https://papascott.de/2005-06-12-a-rose-from-christopher/"}}},"pathContext":{"slug":"/2005-06-12-a-rose-from-christopher/","prev":{"fields":{"title":"'Grandpa Gang' bank robbers jailed","date":"10 Jun 2005","slug":"/2005-06-10--grandpa-gang-bank-robbers-jailed/"}},"next":{"fields":{"title":"We're A Happy Family","date":"12 Jun 2005","slug":"/2005-06-12-were-a-happy-family/"}}}}

/***/ })

